  display: inline-block;

}

.mainContentButton h2 {
  margin-top: 0;
  margin-bottom: 0;
  margin-top: 15px;
  font-weight: 300;
}


.mainContentButton:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background: #000;
  transition: all 0.5s ease;
}

.mainContentButton:hover {
  color: #fff;
}

.mainContentButton:hover:after {
  top: -0.5px;
  height: 101%;
}


