@font-face {
  font-family: 'fontello';
  src: url('../font/fontello.eot?49278361');
  src: url('../font/fontello.eot?49278361#iefix') format('embedded-opentype'),
    url('../font/fontello.woff2?49278361') format('woff2'),
    url('../font/fontello.woff?49278361') format('woff'),
    url('../font/fontello.ttf?49278361') format('truetype'),
    url('../font/fontello.svg?49278361#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}

/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
